    handleOnSearch = (list) => {
        console.log(list)
        if (!list || typeof list !== 'object' || (list && !list.name)) return;
        let arr = this.state.selectedItems;
        arr.push(list);
        this.setState({
            selectedItems: arr
        })
        console.log(arr);
    }

    handleRemoveItem = (index) => {
        let arr = this.state.selectedItems;
        arr.splice(index, 1);
        this.setState({
            selectedItems: arr
        })
    }

    handleOnSaveItems = () => {
        let data = this.state.selectedItems;

        data = data.map(item => {
            let obj = item.value;

            return obj;
        })
        this.props.onSave(data);
        this.setState({
            selectedItems: []
        })
    }

