import React from 'react'
import {
    Dialog 
} from '@material-ui/core';
import { makeStyles, Theme } from '@material-ui/core/styles';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const useStyles = makeStyles((theme: Theme) => ({
	dialog: {
        positioon: 'relative'
    },
	dialogAction: {
		marginBottom: theme.spacing(2),
        marginTop: theme.spacing(2),
        justifyContent:'center'
	},
	dialogContentContainer: {
        width: '100%'
	},
	container: {
        paddingRight:'15px',
        paddingLeft:'15px',
        width: '450px'
	},
	closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
	},
	dialogTitle: {
        marginTop: theme.spacing(2),
        // marginBottom: theme.spacing(2),
        fontSize: '16px',
        color: '#272E4C',
		fontWeight: 700
    },
    main: {
        padding: theme.spacing(5),
        backgroundColor: '#F5F7FB',
        position: 'relative',
        maxWidth: 500
    },
    title: {
        color: '#272E4C',
        fontSize: 24,
        fontWeight: 800,
        marginBottom: 40
    },
    closeIcon: {
        position: 'absolute',
        top: 20,
        right: 20,
        color: '#272E4C',
        zIndex: 99,
        cursor: 'pointer'
    }
}));

interface IProps {
    open: boolean
    onClose: any
    children?: any
    title?: string
}

export const ModalWrapper: React.FC<IProps> = (props: IProps):JSX.Element => {
    const classes = useStyles()

    return (
        <Dialog
			open={props.open}
			onClose={props.onClose}
			aria-labelledby="form-dialog-title"
			maxWidth="md"
			className={classes.dialog}> 

            <FontAwesomeIcon onClick={props.onClose} size="lg" icon={faTimes} className={classes.closeIcon} />

            <div className={classes.main}>
                {
                    props.title && <h2 className={classes.title}>{props.title}</h2>
                }
                { props.children }
            </div>

        </Dialog>
    )
}