import React from 'react';
// import { SearchUserGroup } from 'Components/UI/SearchBar/SearchUserGroup';
import { Button, Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { getUsersToAddToGroup } from 'Pages/API/Calls/User';
import { UI } from 'Components';

interface IState {
  selectedItems: any;
  comboArray: any[];
}

interface IProps {
  onSave: (items) => void;
  onClose: () => void;
}

const useStyles = makeStyles(() => ({
  selectedItems: {
    borderRadius: 20,
    fontSize: 13,
    backgroundColor: '#d1d4db',
    padding: '5px 10px'
  },
  xIcon: {
    fontSize: 16
  }
}));
console.log(useStyles);

export class AddUserGroupBody extends React.Component<IProps, IState> {

  constructor(props) {
    super(props);

    this.state = {
      selectedItems: [],
      comboArray: [],
    };
  }

  componentDidMount() {
    
  }

  handleCloseSideModalBlock = () => {
    this.props.onClose();
  };

  handleOnSearch = (list) => {
    console.log(list)
    if (!list || typeof list !== 'object' || (list && !list.name) || (list && !list.username)) return;
    let arr = this.state.selectedItems;
    arr.push(list);
    this.setState({
      selectedItems: arr
    })
    console.log(arr);
  }

  handleRemoveItem = (index) => {
    let arr = this.state.selectedItems;
    arr.splice(index, 1);
    this.setState({
      selectedItems: arr
    })
  }

  handleOnSaveItems = () => {
    let data = this.state.selectedItems;

    data = data.map(item => {
      let obj = item.value;

      return obj;
    })

    this.props.onSave(data);
    this.setState({
      selectedItems: []
    })
  }

  fetchUserOption = async (input, cb) => {
    let searchResult = await getUsersToAddToGroup(input).catch(() => cb([]));

    if (searchResult) {
      cb(this.userOptionBeautifier(searchResult));
    } else {
      cb([])
    }
  }

  userOptionBeautifier = (option: any) => {
    let options = option.map((o: any) => {
      return {
        value: o,
        label: o.full_name
      }
    })

    return options
  }

  handlePick = (selected) => {
    this.setState({
      selectedItems: selected
    })
  }


  render(): JSX.Element {
    const classes = {
      buttonsContainer: {
        padding: '1em',
      },
      selectedContainer: {
        padding: '3em 0 2em'
      },
      selectedItems: {
        borderRadius: 20,
        fontSize: 13,
        backgroundColor: '#d1d4db',
        padding: '5px 10px',
        margin: '5px',
        display: 'inline-block',
      },
      selectedText: {
        paddingRight: '5px'
      },
      saveButton: {
        width: '135px',
        fontSize: '14px',
        color: '#fff',
      },
      cancelButton: {
        width: '135px',
        fontSize: '14px',
      }
    };

    return (
      <div style={{minHeight: 350, minWidth: 400}}>
        {/* <SearchUserGroup
          searchLabel={'Search user group or account'}
          onSearch={this.handleOnSearch}
          comboBoxMode={true}
          comboListsArray={this.state.comboArray}
        /> */}
        <span>Search user account</span>
        <UI.Dropdown
            async={true}
            onChange={this.handlePick}
            fetchOption={this.fetchUserOption}
            placeholder="Search User"
            icon={true}
            isMulti={true}
            closeOnSelect={true}
            value={this.state.selectedItems}
            choices={[]} />
       
        <Grid container spacing={2} alignItems="center" justify="center" style={{marginTop: 20}}>
          <Grid item xs={6} style={{textAlign: 'center'}}>
            <Button style={classes.cancelButton} variant="outlined" color="secondary" onClick={() => this.handleCloseSideModalBlock()}>Cancel</Button>
          </Grid>
          <Grid item xs={6} style={{textAlign: 'center'}}>
            <Button
              disabled={this.state.selectedItems.length < 1 ? true : false}
              style={classes.saveButton}
              variant="contained"
              color="primary"
              onClick={() => this.handleOnSaveItems()}>
              Add
            </Button>
          </Grid>
        </Grid>
      </div>
    );
  }
}
